import {Component} from '@angular/core';
import {VocabularyService} from "../../services/vocabulary.service";
import {Vocabulary} from "../../models/vocabulary";

@Component({
  selector: 'all-vocabulary',
  templateUrl: './all-vocabulary.component.html',
  providers: [VocabularyService]
})
export class AllVocabularyComponent {
  vocals: Vocabulary[];
  totalPages;
  previous;
  next;
  currentPage;

  constructor(private vocabularyService: VocabularyService) {
    this.onUpdateVocal(1);
  }

  private onUpdateVocal(page) {
    this.vocabularyService.getAll(page).then(response => {
      this.vocals = response.results;
      this.totalPages = Array.from(Array(response.totalPages), (x, i) => i);
      this.previous = response.previous;
      this.next = response.next;
      this.currentPage = response.currentPage;
    })
  }

  onPage = (page) => {
    this.onUpdateVocal(page);
  };

  onNext = () => {
    this.onUpdateVocal(this.currentPage + 1);
  };

  onPrevious = () => {
    this.onUpdateVocal(this.currentPage - 1);
  }


}



