import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';
import {HttpModule} from '@angular/http';

import {AppComponent} from './app.component';
import {HeaderComponent} from './components/header/header.component';
import {FooterComponent} from './components/footer/footer.component';
import {Routing} from './app.routes';
import {HomeComponent} from './pages/home/home.component';
import {TodayComponent} from './pages/today/today.component';
import {NotFoundComponent} from './pages/errors/not-found.component';
import {NavComponent} from './components/nav/nav.component';
import {VocabularyComponent} from './components/vocabulary/vocabulary.component';
import {HttpClientApp} from './services/http-client-app';
import {VocabularySmallComponent} from "./components/vocabulary-small/vocabulary-smaill.component";
import {AllVocabularyComponent} from "./pages/all-vocabulary/all-vocabulary.component";
import {LoginComponent} from "./pages/login/login.component";
import {RegisterComponent} from "./pages/register/register.component";
import {ForgetPassComponent} from "./pages/forget-pass/forget-pass.component";
import {AddVocalComponent} from "./pages/add-vocal/add-vocal.component";

@NgModule({
  declarations: [
    AppComponent, HomeComponent, HeaderComponent, FooterComponent, TodayComponent, NotFoundComponent, NavComponent, VocabularyComponent, VocabularySmallComponent, AllVocabularyComponent, LoginComponent, RegisterComponent, ForgetPassComponent ,AddVocalComponent
  ],
  imports: [
    HttpModule, BrowserModule, Routing
  ],
  providers: [HttpClientApp],
  bootstrap: [AppComponent]
})
export class AppModule {
}



