import {Component, Input} from '@angular/core';
import {Vocabulary} from "../../models/vocabulary";

@Component({
  selector: 'vocabulary-small',
  templateUrl: './vocabulary-small.component.html'
})
export class VocabularySmallComponent {
  private audio;
  @Input() vocal: Vocabulary;

  constructor() {
  }

  ngOnChanges(){
    this.audio = new Audio();
    this.audio.src = this.vocal.audio;
    this.audio.load();
  }

  onPlay = () => {
    this.audio.play();
     console.log('play');
  }

}



